<template>
  <div>
    <div class="row">
      <div class="col-12 col-md-4 my-2 mb-sm-0 align-items-center">
        <h2 class="font-weight-bold">
          Licenses:
          <span>{{todaySales}}</span>
        </h2>
      </div>
      <div
        class="col-12 col-md-4 my-2 mb-sm-0 d-flex justify-content-start justify-content-sm-center align-items-center"
      >
        <input
          type="text"
          v-model="searchText"
          placeholder="License key or PayPal email or activated device"
          class="form-control form-control-sm w-100"
        />
        <div>
          <button
            class="form-control form-control-sm ml-2"
            v-on:click.stop.prevent="searchLicenses"
          >Search</button>
        </div>
      </div>
      <div
        class="col-12 col-md-4 my-2 mb-sm-0 d-flex justify-content-between justify-content-sm-end align-items-center"
      >
        <div>
          <button
            class="btn btn-secondary btn-sm ml-auto mr-2"
            v-on:click.stop.prevent="refresh"
          >Refresh</button>
        </div>
        <b-pagination
          v-model="currentPage"
          :total-rows="total"
          :per-page="perPage"
          size="sm"
          class="float-xs-left float-sm-right my-0"
        ></b-pagination>
      </div>
    </div>

    <div class="row">
      <div
        class="col-12 col-sm-3 col-md-4 col-lg-3 py-2"
        v-for="item in items"
        v-bind:key="item.license.id"
      >
        <div
          class="card license-card border-2"
          :class="{ 'border-success': item.payment && isTodaySale(item.payment.createTime) }"
        >
          <div class="card-body">
            <div class="row">
              <div class="col-12 d-flex justify-content-between align-items-center">
                <div>#{{item.license.id}}</div>
                <div class>{{item.license.validity}} Months</div>
                <svg
                  v-if="item.payment && item.payment.fromMethod === 'webhook'"
                  class="webhook-flag"
                />
                <div
                  :class="{'basic-flag': item.license.plan == 'BASIC', 'pro-flag': item.license.plan == 'PRO'}"
                >{{item.license.plan}}</div>
                <div>
                  <div
                    class="badge badge-pill"
                    :id="'popover-'+item.license.id"
                    v-bind:class="{ 'badge-success': item.license.status=='NORMAL', 'badge-danger': item.license.status=='REFUNDED', 'badge-warning': item.license.status=='REVOKED' }"
                  >{{item.license.status}}</div>

                  <b-popover
                    :target="'popover-'+item.license.id"
                    triggers="hover click"
                    placement="bottom"
                  >
                    <div>
                      <div>
                        PayPal Order Id:
                        <span v-if="item.payment">{{item.payment.paypalOrderId}}</span>
                      </div>
                      <div>
                        PayPal Capture Id:
                        <span v-if="item.payment">{{item.payment.paypalCaptureId}}</span>
                      </div>
                      <div>
                        PayPal Refund Id:
                        <span v-if="item.payment">{{item.payment.paypalRefundId}}</span>
                      </div>
                      <div>
                        PayPal Refund Time:
                        <span
                          v-if="item.payment"
                        >{{formatDate(item.payment.refundTime)}}</span>
                      </div>
                    </div>
                  </b-popover>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-12">
                <div class="license-key">{{item.license.licenseKey}}</div>
              </div>
            </div>
            <div class="row">
              <div class="col-10 d-flex justify-content-start align-items-center">
                Expires:
                <span v-if="item.license.expireTime">{{formatDate(item.license.expireTime)}}</span>
              </div>
              <div class="col-2 d-flex justify-content-end align-items-center">
                <b-dropdown variant="button" no-caret right>
                  <template slot="button-content">
                    <octicon name="gear"></octicon>
                  </template>

                  <template v-if="isAdmin">
                    <b-dropdown-item
                      v-if="isActivationLocked(item.license.activationLockTo)"
                      v-on:click.stop.prevent="unlockActivation(item.license.id)"
                    >Unlock Activation</b-dropdown-item>
                    <b-dropdown-item
                      v-if="!item.payment"
                      v-on:click.stop.prevent="attachLicenseToEmail(item.license.id)"
                    >Attach To Email</b-dropdown-item>
                    <b-dropdown-divider></b-dropdown-divider>
                    <b-dropdown-item v-on:click.stop.prevent="refund(item.license.id)">Refund</b-dropdown-item>
                    <b-dropdown-item v-on:click.stop.prevent="revoke(item.license.id)">Revoke</b-dropdown-item>
                    <b-dropdown-item v-on:click.stop.prevent="deletee(item.license.id)">Delete</b-dropdown-item>
                  </template>
                </b-dropdown>
              </div>
            </div>
            <div class="border p-2 my-2">
              <template>
                <div>
                  Activated Device:
                  <span v-if="item.activation">{{item.activation.deviceSign}}</span>
                </div>
                <div>
                  Locked To:
                  <span
                    v-if="item.activation && isActivationLocked(item.license.activationLockTo)"
                  >{{formatDate(item.license.activationLockTo)}}</span>
                </div>
              </template>
            </div>
            <div class="border p-2 my-2">
              <div>
                Payee:
                <span
                  v-if="item.payment"
                  v-bind:class="{ 'text-warning': item.payment.payeeEmail!='niedelian@gmail.com' }"
                >&lt;{{item.payment.payeeEmail}}&gt;</span>
              </div>
              <div>
                Amount
                <span v-if="item.payment">{{item.payment.amount}} {{item.payment.currency}}</span>
              </div>
              <div>
                Paid Time:
                <span v-if="item.payment">
                  <strong>{{formatDate(item.payment.createTime)}}</strong>
                </span>
              </div>
              <div>
                Payer:
                <span v-if="item.payment">{{item.payment.payerName}}</span>
              </div>
              <div>
                Email:
                <span v-if="item.payment">&lt;{{item.payment.payerEmail}}&gt;</span>
              </div>
              <div class="address-field">
                Address:
                <template v-if="item.payment && item.payment.payerAddress">
                  <span
                    :id="'popover-address-'+item.license.id"
                  >{{item.payment.payerAddress | truncate(60)}}</span>
                  <b-popover
                    :target="'popover-address-'+item.license.id"
                    triggers="hover click"
                    placement="bottom"
                  >{{item.payment.payerAddress}}</b-popover>
                </template>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row pb-5 pt-3">
      <div class="col-12 d-flex justify-content-center">
        <b-pagination
          v-model="currentPage"
          :total-rows="total"
          :per-page="perPage"
          size="sm"
          class="float-xs-left float-sm-right my-0"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import * as config from "../../config";
import * as moment from "moment";
import axios from "axios";
import { EventBus } from "../event-bus.js";

export default {
  name: "LicenseList",
  data() {
    return {
      items: [],
      total: 0,
      perPage: 50,
      currentPage: 1,
      approved: "all",
      searchText: null
    };
  },
  created() {
    let self = this;

    this.load();

    EventBus.$on("licenses-created", () => {
      self.load();
    });
  },
  computed: {
    ...mapGetters({
      user: "getUser",
      isAdmin: "isAdmin",
      isLoggedIn: "isLoggedIn"
    }),
    todaySales() {
      return !this.items
        ? 0
        : this.items.filter(
            item =>
              item.payment &&
              item.payment.status == "NORMAL" &&
              moment.utc(item.payment.createTime).local().isSame(moment.utc().local(), 'day')
          ).length;
    }
  },
  watch: {
    currentPage: function() {
      this.load();
    }
  },
  methods: {
    refresh() {
      EventBus.$emit("reload-daysales");
      this.load();
    },
    isOwner(script) {
      return +script.user.id === +this.user.id;
    },
    isTodaySale(createTime) {
      return (
        createTime &&
        moment.utc(createTime).local().isSame(moment.utc().local(), 'day')
      );
    },
    formatDate(date) {
      if (!date) return null;
      const d = moment
        .utc(date)
        .local()
        .format("YYYY-MM-DD HH:mm:ss Z");

      return d ? d : date;
    },
    routeTo(to) {
      this.$router.push(to);
    },
    isActivationLocked(activationLockTo) {
      if (!activationLockTo) return false;
      const activationLockToTime = new Date(activationLockTo);
      const utcNow = moment()
        .utc()
        .toDate();
      return utcNow < activationLockToTime;
    },
    load() {
      let self = this;

      self.$store.commit("setShowLoading", true);

      const url = `${config.server}/licenses`;

      axios
        .get(url, {
          params: {
            page: this.currentPage - 1,
            limit: this.perPage
          }
        })
        .then(resp => {
          const { data } = resp;
          self.items = data.items;
          self.total = data.total;
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .finally(() => {
          self.$store.commit("setShowLoading", false);
        });

      return;
    },
    searchLicenses() {
      const self = this;

      const url = `${config.server}/licenses/search`;

      axios
        .get(url, {
          params: {
            searchText: self.searchText
          }
        })
        .then(function(response) {
          const { items, total } = response.data;
          self.items = items;
          self.total = total;
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .finally(function() {
          self.queried = true;
        });
    },
    unlockActivation(licenseId) {
      let self = this;

      self.$store.commit("setShowLoading", true);

      const url = `${config.server}/licenses/${licenseId}/unlockActivation`;

      axios
        .put(url)
        .then(() => {
          self.$notify({ group: "main", type: "success", text: "Successful!" });
          self.load();
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .finally(() => {
          self.$store.commit("setShowLoading", false);
        });
    },
    refund(licenseId) {
      let self = this;

      self.$store.commit("setShowLoading", true);

      const url = `${config.server}/licenses/${licenseId}/refund`;

      axios
        .put(url)
        .then(() => {
          self.$notify({ group: "main", type: "success", text: "Successful!" });
          self.load();
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .finally(() => {
          self.$store.commit("setShowLoading", false);
        });

      return;
    },
    revoke(licenseId) {
      let self = this;

      self.$store.commit("setShowLoading", true);

      const url = `${config.server}/licenses/${licenseId}/revoke`;

      axios
        .put(url)
        .then(() => {
          self.$notify({ group: "main", type: "success", text: "Successful!" });
          self.load();
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .finally(() => {
          self.$store.commit("setShowLoading", false);
        });

      return;
    },
    deletee(licenseId) {
      let self = this;

      self.$store.commit("setShowLoading", true);

      const url = `${config.server}/licenses/${licenseId}/delete`;

      axios
        .put(url)
        .then(() => {
          self.$notify({ group: "main", type: "success", text: "Successful!" });
          self.load();
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .finally(() => {
          self.$store.commit("setShowLoading", false);
        });
    },
    attachLicenseToEmail(licenseId) {
      let self = this;

      let email = prompt("Attach license to email:", "");
      if (!email) {
        return;
      }
      email = email.trim();

      self.$store.commit("setShowLoading", true);

      const url = `${config.server}/licenses/${licenseId}/attach?email=${email}`;

      axios
        .put(url)
        .then(() => {
          self.$notify({ group: "main", type: "success", text: "Successful!" });
          self.load();
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .finally(() => {
          self.$store.commit("setShowLoading", false);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.license-key {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: 600;
  font-size: 18px;
}

.address-field {
  height: 50px;
}

.webhook-flag {
  background-image: url(../assets/webhook.svg);
  width: 16px;
  height: 16px;
  filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg)
    brightness(104%) contrast(97%);
}

.license-card {
  .basic-flag {
    color: #2060fe;
    text-shadow: 0 0 2px 0px #144cdb;
    font-size: 16px;
    font-weight: 600;
  }

  .pro-flag {
    color: #d6491a;
    text-shadow: 0 0 2px 0px #d6491a;
    font-size: 16px;
    font-weight: 600;
  }
}
</style>
