<template>
  <div>
    <h2 class="font-weight-bold">Statis</h2>
    <div class="p-3 border">
      <div class="row">
        <div class="col-12 col-sm-2 d-flex justify-content-center align-items-center">
          <div
            class="main-numbers d-flex flex-row flex-sm-column justify-content-center align-items-center"
          >
            <div class="mr-2 mr-sm-0">{{todaySales}}</div>
            <div>{{yesterdaySales}}</div>
          </div>
        </div>
        <div class="col-12 col-sm-10">
          <div class="d-flex flex-row justify-content-start align-items-center">
            <div class="mr-2">Last days</div>
            <div>
              <select v-model="days" class="form-control">
              <option value="7">7</option>
              <option value="15">15</option>
              <option value="30">30</option>
              <option value="60">60</option>
              <option value="180">180</option>
              <option value="360">360</option>
              <option value="720">720</option>
            </select>
            </div>
          </div>
          <DaySalesChart :chart-data="daySalesData" :options="daySalesOptions" :height="150"></DaySalesChart>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DaySalesChart from "./DaySalesChart.js";
import { EventBus } from "../event-bus.js";

const options = {
  responsive: true
};

const randomInt = function(minimum, maximum) {
  return Math.floor(Math.random() * (maximum - minimum + 1)) + minimum;
};

const randomColorSet = function(count) {
  const colorSet = Array.from(Array(count)).map(() => {
    const r = randomInt(0, 255);
    const g = randomInt(0, 255);
    const b = randomInt(0, 255);
    return [`rgba(${r}, ${g}, ${b}, 0.4)`, `rgba(${r}, ${g}, ${b}, 0.6)`];
  });
  const backgroundColors = colorSet.map(c => c[0]);
  const borderColors = colorSet.map(c => c[1]);
  return [backgroundColors, borderColors];
};

export default {
  name: "Statis",
  components: {
    DaySalesChart
  },
  data() {
    return {
      days: 30,
      daySalesData: {},
      daySalesOptions: options,
      todaySales: 0,
      yesterdaySales: 0
    };
  },
  mounted() {
    this.loadDaySales();
    EventBus.$on("reload-daysales", () => {
      this.loadDaySales();
    });
  },
  watch: {
    days: function() {
      this.loadDaySales();
    }
  },
  methods: {
    loadDaySales() {
      const self = this;

      self.$store.commit("setShowLoading", true);

      const url = `/statis/daySales?days=${self.days}`;
      self.$http
        .get(url)
        .then(function(response) {
          const rawData = response.data.reverse();
          const labels = rawData.map(d => d.day);
          const counts = rawData.map(d => d.count);
          // const amounts = rawData.map(d => d.amount);

          self.todaySales = counts[labels.length - 1];
          self.yesterdaySales = counts[labels.length - 2];

          const colorSet1 = randomColorSet(labels.length);

          self.daySalesData = {
            labels: labels,
            datasets: [
              {
                label: "Count",
                data: counts,
                backgroundColor: colorSet1[0],
                borderColor: colorSet1[1],
                borderWidth: 1
              }
            ]
          };
        })
        .catch(function(error) {
          if (error.response) {
            self.$notify({
              group: "main",
              type: "error",
              text: error.response.data.message
            });
          } else {
            self.$notify({ group: "main", type: "error", text: error });
          }
        })
        .finally(() => {
          self.$store.commit("setShowLoading", false);
        });
    }
  }
};
</script>

